import { Watchlist } from "@rp/common/api-types"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { EditableWatchlistFields } from "@rp/react/web/common/providers"
import { Box } from "@rp/react/web/mui/core"
import { Edit } from "@rp/react/web/mui/icons"

const WatchlistDescription = ({
  canEdit,
  watchlist,
  rename,
}: {
  canEdit: boolean
  watchlist: Watchlist
  rename: {
    onClick: (watchlist: Watchlist, autoFocus: EditableWatchlistFields) => void
  }
}) => {
  const hasDescription = (watchlist.description?.trim().length ?? 0) > 0
  const showDescription = hasDescription || canEdit

  const description = watchlist.description?.trim().length
    ? watchlist.description
    : "Add a description"

  return (
    <Box
      onClick={() => canEdit && rename.onClick(watchlist, "description")}
      sx={{
        display: "flex",
        gap: 1,
        alignItems: "flex-start",
        cursor: canEdit ? "pointer" : "default",
        minHeight: 42,
        "&:hover svg": {
          visibility: "visible",
        },
      }}
    >
      {showDescription && (
        <EllipsisTooltip
          tooltipText={description}
          vertical
          numberOfLines={2}
          typographyProps={{
            fontWeight: 400,
            fontSize: 14,
            color: "text.secondary",
          }}
        >
          {description}
        </EllipsisTooltip>
      )}
      {canEdit && (
        <Edit
          fontSize="small"
          sx={{
            color: "text.secondary",
            visibility: "hidden",
          }}
        />
      )}
    </Box>
  )
}

export { WatchlistDescription }
